import { _localStorage } from './_localStorage';
import { _config } from './_config';

/**
 * HTML
 * @param {*} selector
 * @param {*} filepath
 */
export const includeHTML = (selector, filepath) => {
  $(function () {
    $.ajaxSetup({ cache: false });
    $(selector).load(filepath);
  });
};

